
.home-banner-container {
    margin-top: 3px;
    width: 100%;
    height: 850px;
    background: url("../../assets/york-background-trimmed-2.jpg");
    background-position: center;
}

.center-left {
    position: absolute;
    top: 35%;
    left: 8%;
}

.home-banner-text {
    color: white;
    text-align: center;
    cursor:default;
}

.home-banner-text.header {
    font-size: 2.7rem;
}

.home-banner-text.body {
    margin-top: 10px;
    margin-bottom: 30px;
    font-size: 1.6rem;
}

.donate-button-div {
    padding-left: 20%;
}

.donate-button {
    background-color: #1a6d45;
    padding: 20px;
    width: 293px;
    font-size: 16px;
    border: 2px solid white;
    border-radius: 40px;
    cursor: pointer;
    margin: 4px 2px;
}

.donate-button-text {
    color: white;
}

/* Mobile Overrides */
@media screen and (max-width: 960px) {

    .home-banner-container {
        max-height: 500px;
    }

    .center-left {
        position: absolute;
        top: 30%;
        left: 20%;
    }
    
}
