

.NavbarItems {
    background: #1a6d45;
    height: 80px;
    display: flex;

    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}

.fa-react {
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(10, auto);
    grid-gap: 13px;
    list-style: none;
    text-align: center;
    width: 700vw;
    justify-content: center;
}

.nav-link-separator {
    width: 2px;
    height: 60%;
    margin-top: 11px;
    background: white;
}

.nav-links {
    color: white;
    display: flex;
    position: relative;
    text-decoration: none;
    padding: 0.9rem 1rem;
}

.nav-links:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: white;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
}

.nav-links:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

.nav-logo {
    background: white;
    background-image: url("../../assets/logo-simple-small.png");
    justify-self: left;
    margin-left: 0px;
    font-size: 0;
    cursor: pointer;
}

.fa-bars {
    color: white;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    
    .NavbarItems {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 80px;
        opacity: 0;
        left: -100%;
        transition: all 0.5 ease;
    }

    .nav-menu.active {
        background: #1a6d45;
        left: 0;
        opacity: 1;
        transition: left 0.5s ease, opacity 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 1rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        background-color: #52855a;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        color: white;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times {
        columns: white;
        font-size: 2rem;
    }
    
}