
.welcome-text {
    text-align: center;
    padding-right: 40px;
    padding-left: 40px;
}

.welcome-text.header {
    max-width: 840px;
    font-size: 40px;
    padding-bottom: 40px;
}

.welcome-text.body {
    max-width: 840px;
    font-size: 25px;
}

.welcome-text.body + .welcome-text.body {
    margin-top: 20px;
}

.learn-more-button {
    color: white;
    background-color: #1a6d45;
    width: 100%;
    min-height: 80px;
    border: 3px solid white;
    margin-top: 40px;
    font-size: 1.8rem;
    padding: 5px 75px;
    cursor: pointer;
}
