
.contact-banner-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: #1a6d45;
}

.contact-banner-form-container {
    display: flex;
    flex-direction: column;
    margin: 30px 10%;
    width: 30%;
}

.contact-banner-text-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #1a6d45;
    color: white;
    min-width: 30%;
    max-width: 40%;
    max-height: 800px;
    flex-grow: 1;
}

.contact-form-submit-container {
    display: flex;
    flex-direction: column;

}

.contact-text {
    color: white;
    width: 600px;
}

.contact-text.header {
    font-size: 40px;
}

.contact-text.body {
    max-width: 840px;
    font-size: 25px;
}

.contact-text + .contact-text {
    margin-top: 20px;
}

.contact-form-label {
    color: white;
    left: 0;
    margin-top: 10px;
}

.contact-input-field {
    width: 100%;
    outline: none;
    padding-right: 3px;
    padding-left: 3px;
}

.contact-input-field.details {

}

.contact-input-field.message {
    height: 200px;
}

.contact-recaptcha {
    margin: 20px;
    align-self: center;
}

.contact-form-submit-button {
    color: white;
    background-color: #1a6d45;
    min-height: 30px;
    border: 3px solid white;
    text-align: center;
    font-size: 1rem;
    padding: 5px 75px;
    align-self: center;
    cursor: pointer;
}

.submit-message-text {
    color: white;
    outline: 2px solid white;
    text-align: center;
    padding: 10px 10px;
}

.contact-social-media-container {
    display: flex;
    flex-direction: row;
}

.contact-social-media-icon {
    margin-top: 30px;
    margin-left: 15px;
    margin-right: 15px;
    width: 50px;
    height: 50px;
}

/* Mobile Overrides */
@media screen and (max-width: 960px) {

    .contact-banner-container {
        flex-direction: column;
    }
 
    .contact-form-submit-button {
        width: 100%;
    }

}
