.information-banner-container {
    display: flex;
    max-height: 805px;
    background-color: #1a6d45;
}

.information-banner-container.image-left {
    flex-direction: row;
}

.information-banner-container.image-right {
    flex-direction: row-reverse;
}

.information-banner-item {
    overflow: hidden;
}

.information-banner-item.text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #1a6d45;
    color: white;
    min-width: 30%;
    max-height: 800px;
    flex-grow: 1;
}

.information-banner-item.image {
    min-width: 0;
    max-height: 805px;
}

@media screen and (max-width: 960px) {
    
    .information-banner-container {
        padding-bottom: 3px;
    }

    .information-banner-item.image {
        display: none;
    }

    .information-banner-item.text {
        padding-bottom: 10px;
    }

}